export type Office365Folder = {
  id: string;
  displayName?: string;
};

export type Office365FolderList = {
  value?: Array<Office365Folder>;
};

export type Office365Mail = {
  id: string;
  internetMessageId?: string;
  subject?: string;
  receivedDateTime?: string;
  sentDateTime?: string;
  lastModifiedDateTime?: string; // <-- important for moves/edits
  body?: { contentType: 'Text' | 'HTML'; content: string };
  bodyPreview?: string;
  from?: { emailAddress: { address?: string } };
  replyTo?: Array<{ emailAddress: { address?: string } }>;
  toRecipients?: Array<{ emailAddress: { address?: string } }>;
  ccRecipients?: Array<{ emailAddress: { address?: string } }>;
  bccRecipients?: Array<{ emailAddress: { address?: string } }>;
  internetMessageHeaders?: Array<{ name: string; value: string }>;
};

export type Office365MailList = {
  value?: Array<Office365Mail>;
  '@odata.nextLink'?: string;
};

export type Office365MailCategories = {
  categories?: Array<string>;
};

export type Office365MailAttachment = {
  id: string;
  contentType?: 'message/rfc822' | string;
  name?: string;
  fileName?: string;
  contentBytes?: unknown;
  '@odata.type':
    | '#microsoft.graph.fileAttachment'
    | '#microsoft.graph.itemAttachment'
    | string;
  item?: {
    '@odata.type': '#microsoft.graph.message' | string;
  };
};

export type Office365MailAttachmentList = {
  value?: Array<Office365MailAttachment>;
  '@odata.nextLink'?: string;
};

export const WELL_KNOWN_EMAIL_BOXES = [
  'inbox',
  'drafts',
  'sent items',
  'deleted items',
  'archive',
  'junk email',
];

export const EMAIL_SELECTABLE_FIELD = [
  'id',
  'internetMessageId',
  'subject',
  'receivedDateTime',
  'lastModifiedDateTime',
  'body',
  'bodyPreview',
  'from',
  'replyTo',
  'toRecipients',
  'ccRecipients',
  'bccRecipients',
  'internetMessageHeaders',
];
